<template>
  <bee-menu :follow-target='followDom' :scroll-dom='scrollDom' :autoHide='!multiple' ref='menu'>
    <div class='options--wp' :style='{minWidth: `${textContainerWidth}px`}' ref='wrapper'>
      <ul v-if='!scrollRect'>
        <!-- has options -->
        <template v-if='options.length'>
          <li :class='["options--item", {
            "options__selected": isSelectedItem(option),
            "options__multiple": multiple
            }]'
            :style='{lineHeight: (textContainerHeight - 2) + "px"}'
            v-for='(option, index) in options'
            @click='pickerItem(option, index)'
            :key='"option_item_" + deepValue(optionKey.value, option)'
          >
            {{deepValue(optionKey.label, option)}}
            <bee-icon class='multiple--icon' icon='correct' v-if='multiple'></bee-icon>
          </li>
        </template>

        <!-- no options -->
        <li v-if='!options.length' class='options--item' :style='{lineHeight: (textContainerHeight - 2) + "px"}'></li>
      </ul>

      <bee-scroll show-type='hover' v-else ref='ScrollRect'>
        <ul>
          <!-- has options -->
          <template v-if='options.length'>
            <li :class='["options--item", {
              "options__selected": isSelectedItem(option),
              "options__multiple": multiple
              }]'
              :style='{lineHeight: (textContainerHeight - 2) + "px"}'
              v-for='(option, index) in options'
              @click='pickerItem(option, index)'
              :key='"option_item_" + deepValue(optionKey.value, option)'
            >
              {{deepValue(optionKey.label, option)}}
              <bee-icon class='multiple--icon' icon='correct' v-if='multiple'></bee-icon>
            </li>
          </template>

          <!-- no options -->
          <li v-if='!options.length' class='options--item' :style='{lineHeight: (textContainerHeight - 2) + "px"}'></li>
        </ul>
      </bee-scroll>
    </div>
  </bee-menu>
</template>

<script>
import { deepValue } from '../../utils/object'

export default {
  name: 'BeeSelectOptions',
  data () {
    return {
      deepValue: deepValue,
      scrollRect: false
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.scrollRect = this.$refs.wrapper.scrollHeight !== this.$refs.wrapper.clientHeight
    })
  },
  updated () {
    this.$nextTick(() => {
      // menu
      this.$refs.menu.setPosition()
    })
  },
  methods: {
    /**
     * 
     * @param  {Object} option options item
     * @param  {Number} index  item
     */
    pickerItem (option, index) {
      this.pickerCallBack(option, index)
    },

    /**
     * option
     * @param  {Object} option option
     */
    isSelectedItem (option) {
      if (this.value === null && this.value === undefined) return

      if (this.value instanceof Array) {
        // 
        return this.value.indexOf(this.deepValue(this.optionKey.value, option)) !== -1
      } else {
        // 
        return this.deepValue(this.optionKey.value, option) === this.value
      }
    }
  }
}
</script>

<style lang="less">
@import '../../theme.less';

@root: options;

.@{root}--wp {
  list-style: none;
  min-width: @select-min-width;
  border-radius: @border-radius;
  background-color: @select-options-bg-color;
  overflow: hidden;
  color: @font-tint-color;
  max-height: 28px * 8 + 2px;

  .scroll--body {
    max-height: 28px * 8 + 2px;
  }

  .@{root}--item {
    line-height: 28px;
    list-style: none;
    cursor: pointer;
    padding: 0 8px;
    transition: background 0.2s;
    white-space: nowrap;

    &.@{root}__multiple {
      padding-right: 30px;

      .multiple--icon {
        float: right;
        margin-right: -16px;
        font-size: 13px;
        display: none;
      }
    }

    &.@{root}__selected {
      color: @font-color;

      .multiple--icon {
        display: block;
      }
    }

    &:hover {
      background-color: @select-options-hover-bg-color;
    }
  }
}
</style>
